<template>
    <div class="song-list-album">
        <div class="album-slide">
            <div class="album-img">
                <img :src="attachImageUrl(songList.pic)">
            </div>
            <div class="album-info">
                <h2>简介:</h2>
                <span>
                    {{songList.introduction}}
                </span>
            </div>
        </div>
        <div class="album-content">
            <div class="album-title">
                <p>{{songList.title}}</p>
            </div>
            <div class="album-score">
                <div>
                    <h3>歌单评分:</h3>
                    <div>
                        <el-rate v-model="average"  disabled></el-rate>
                    </div>
                </div>
                <span>{{average*2}}</span>
                <div>
                    <h3>为这个歌单评个分吧:</h3>
                    <div @click="setRank">
                        <el-rate v-model="rank" allow-half show-text></el-rate>
                    </div>
                </div>
            </div>
            <div class="songs-body">
                 <album-content :songList="listOfSongs">
                        <template slot="title">歌曲列表</template>
                 </album-content>
                 <comment :playId="songListId" :type="1"></comment>
            </div>
        </div>
    </div>
</template>

<script>
import {mixin} from '../mixins';
import {mapGetters} from 'vuex';
import {listSongDetail,songOfSongId,setRank,getRankOfSongListId} from '../api/index';
import AlbumContent from '../components/AlbumContent.vue'
import Comment from '../components/Comment.vue';
export default {
    name: 'song-list-album',
    mixins: [mixin],
    components:{
        AlbumContent,
        Comment
    },
    data(){
        return {
            songLists: [],  //当前页面需要展示的歌曲列表
            songListId: '',  //传入的歌单id
            songList: '', //当前歌曲对象
            average: 50,  //评价平均分
            rank: 0  //提交评价的分数
        }
    },
    computed:{
        ...mapGetters([
            'listOfSongs', //当前播放列表
            'tempList',  //当前的歌单对象,但刷新后会失效，所以使用localStorage代替
            'loginIn',  //用户是否已经登陆
            'userId',  //当前登录用户id
        ])
    },
    created(){
        this.songListId = this.$route.params.id;  //传过来的id
        this.songList = JSON.parse(window.localStorage.getItem("item"));
        this.getSongId();
        this.getRank(this.songListId);
    },
    methods:{
        //获取当前歌单的歌曲列表
        getSongId(){
            listSongDetail(this.songListId)
            .then(res =>{
                for(let item of res){
                    this.getSongList(item.songId);
                }
                this.$store.commit('setListOfSongs',this.songLists);
            })
             .catch(err => {
                console.log(err)
            })
        },

        //根据歌曲id获取歌曲信息
        getSongList(id){
            songOfSongId(id)
            .then(res =>{
                this.songLists.push(res);
            })
            .catch(err => {
                console.log(err)
            })
        },
        //获取歌单评分
        getRank(id){
            getRankOfSongListId(id)
            .then(res => {
                this.average = res/2;
            })
            .catch(err => {
                console.log(err)
            })
        },

        //提交评分
        setRank(){
            if(this.loginIn){
                let params = new URLSearchParams();
                params.append('songListId',this.songListId);
                params.append('consumerId',this.userId);
                params.append('score',this.rank*2);
                setRank(params)
                .then(res => {
                    if(res.code==1){
                        this.notify('评分成功','success');
                        this.getRank(this.songListId);
                    }else if(res.code==3){
                        this.notify('您已经评分过了','error')
                        this.rank=res.score;
                    }else{
                        this.notify('评分失败','error')
                    }
                })
                .catch(err => {
                     this.notify('评分失败','error')
                })
            }else{
                this.rank = 0;
                this.notify('请先登录','warning');
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../assets/css/song-list-album.scss";
</style>


